import { InnerBlocks } from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"

registerBlockType("voblocktheme/slideshow", {
  title: "Slideshow",
  supports: {
    align: ["full"]
  },
  attributes: {
    align: { type: "string", default: "full" }
  },
  edit: EditComponent,
  save: SaveComponent
})

function EditComponent() {
  return (
    <div style={{ backgroundColor: "#333", padding: "35px" }}>
      <p style={{ textAlign: "center", fontSize: "20px", color: "#FFF" }}>Slideshow</p>
      <div class="banner-img hidden-xs-down">
        <InnerBlocks allowedBlocks={["voblocktheme/slide"]} />
      </div>
    </div>
  )
}

function SaveComponent() {
  return <InnerBlocks.Content />
}